/* global styles */
:root {
    --clr-bcg: #fff;
    --clr-font: #282c35;
    --clr-primary: #2980b9;
}
  
.dark-theme {
    --clr-bcg: #282c35;
    --clr-font: #fff;
    --clr-primary: #3498db;
}
.light-theme {
    --clr-bcg: #fff;
    --clr-font: #282c35;
    --clr-primary: #2980b9;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html{
    scroll-behavior: smooth ;
}
body{
    font-family: 'Roboto Mono', monospace;
    background: var(--clr-bcg);
}
ul{
    list-style: none;

}
a{
    text-decoration: none;
    color: inherit;
}
h1,h2,h3,h4,h5{
    text-transform: capitalize;
    font-family: 'Roboto', sans-serif;
    color: var(--clr-font);
    margin-bottom: 1rem;
    letter-spacing: 2px;
}
.title{
    color: var(--clr-font);
}

p{
    font-family: 'Roboto Mono', monospace;
}
.logo{
    font-family: 'Rajdhani', sans-serif;
    font-size: clamp(1.5rem, -0.8750rem + 8.3333vw, 3rem);
    color: var(--clr-font);
}
.container{
    width: 100%;
    padding: 0 50px;
    max-width: 1100px;
    margin: 0 auto;

}

@media screen and (max-width:500px){
    .container{
        padding: 0 15px;
    }
}
.btn{
    padding: 0.5rem 0.7rem;
    border: none;
    outline: none;
    background: var(--clr-primary);
    color: var(--clr-font);
    font-size: 1.2rem;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 2rem;
    text-transform: capitalize;
    transition: all 0.3s linear;

}

.btn:hover{
    transform: scale(0.98);
    opacity: 0.9;
}